import { Alert, Button, Col, Input, Row, Spinner } from '@sieloapp/components';
import image from '@sieloapp/shared/images/img17.png';
import {
  StyledWrap,
  StyledImg,
  StyledTitle,
  StyledDesc,
  StyledBtnWrap,
} from './style';
import {
  CognitoUserPool,
  CognitoUser,
  AuthenticationDetails,
  ICognitoUserData,
} from 'amazon-cognito-identity-js';
import { Auth } from '@aws-amplify/auth';
import { useCallback, useMemo, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { toggleUser } from '../../redux/slices/user';
import { useForm, SubmitHandler } from 'react-hook-form';
import { hasKey } from '@sieloapp/shared/methods';
import { RootState } from '../../redux/store';
import xorCipher from '../../../src/helpers/crypt';

interface CognitoError {
  message: string;
}

interface IFormValues {
  verificationCode: string;
}
interface alertColor {
  alertColor:
    | 'primary'
    | 'secondary'
    | 'success'
    | 'danger'
    | 'warning'
    | 'info'
    | 'light'
    | 'dark'
    | undefined;
}

const VerifyContainer = () => {
  const [msg, setMessage] = useState('');
  const [alertColor, setAlertColor] = useState(
    'primary' as alertColor['alertColor'],
  );
  const [checkingCode, setcheckingCode] = useState(false);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const storedUserData = useSelector((state: RootState) => state.user);
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<IFormValues>();
  const userName = storedUserData.email;
  const userPassword = xorCipher(storedUserData.password as string);

  const poolData = useMemo(
    () => ({
      UserPoolId: storedUserData.userPoolId as string,
      ClientId: storedUserData.clientId as string,
    }),
    [storedUserData.userPoolId, storedUserData.clientId],
  );

  const onSubmit: SubmitHandler<IFormValues> = async (data) => {
    const verificationCode = data.verificationCode;
    const userPool = new CognitoUserPool(poolData);
    try {
      setcheckingCode(true);
      await Auth.confirmSignUp(userName as string, verificationCode);
      const userData = {
        Username: userName,
        Pool: userPool,
      };
      const authenticationData = {
        Username: userName as string,
        Password: userPassword,
      };
      const user = new CognitoUser(userData as ICognitoUserData);
      const authenticationDetails = new AuthenticationDetails(
        authenticationData,
      );
      user.authenticateUser(authenticationDetails, {
        onSuccess: (result) => {
          console.log(result);
          dispatch(
            toggleUser({
              userName: storedUserData.email,
              email: storedUserData.email,
              firstName: storedUserData.firstName,
              lastName: storedUserData.lastName,
              role: 'user',
              token: storedUserData.token,
              company: 0,
              verified: true,
              clientId: storedUserData.clientId,
              userPoolId: storedUserData.userPoolId,
              password: undefined,
            }),
          );
          navigate('/');
          setcheckingCode(false);
        },
        onFailure: (err: CognitoError) => {
          setAlertColor('danger');
          setMessage(err.message);
          setcheckingCode(false);
        },
      });
    } catch (error) {
      const typedError = error as Error;
      setAlertColor('danger');
      setMessage(typedError.message);
      setcheckingCode(false);
    }
  };

  const resendVerification = useCallback(() => {
    setMessage('');
    if (!userName) return;
    const userPool = new CognitoUserPool(poolData);
    const userData = {
      Username: userName,
      Pool: userPool,
    };
    const user = new CognitoUser(userData as ICognitoUserData);
    user.resendConfirmationCode((err) => {
      if (err) {
        setAlertColor('danger');
        setMessage(err.message);
      } else {
        setAlertColor('success');
        setMessage('Verification code sent');
      }
    });
  }, [userName, poolData]);

  return (
    <StyledWrap>
      <StyledImg>
        <img src={image} alt="verify" />
      </StyledImg>
      <StyledTitle>Verify your email address</StyledTitle>
      <StyledDesc>
        Please check your email:{' '}
        <a href={`mailto:${userName ?? ''}`}>{userName ?? ''}</a> and copy the
        code sent. Type or paste the code in the box below and hit the button to
        confirm your account.
      </StyledDesc>
      {msg !== '' && <Alert color={alertColor}>{msg}</Alert>}
      <form action="#" onSubmit={handleSubmit(onSubmit)} noValidate>
        <Row gutters={10}>
          <Col col={6}>
            <Input
              type="text"
              id="verificationCode"
              placeholder="Verification Code"
              mb="0px"
              feedbackText={errors?.verificationCode?.message}
              state={hasKey(errors, 'verificationCode') ? 'error' : 'success'}
              showState={!!hasKey(errors, 'verificationCode')}
              {...register('verificationCode', {
                required: 'Verification code is required',
                pattern: {
                  value: /^\d{6}$/,
                  message: 'Invalid verification code',
                },
              })}
            />
          </Col>
          <Col col={6}>
            <Button type="submit" color="dark" ml="0px" fullwidth={true}>
              {checkingCode ? (
                <div className="spin">
                  <Spinner size="xs" color="light" />
                </div>
              ) : (
                'Submit'
              )}
            </Button>
          </Col>
        </Row>
      </form>
      <StyledBtnWrap>
        <Button
          onClick={resendVerification}
          color="white"
          variant="texted"
          mr="10px"
        >
          Resend Verification
        </Button>
        |
        <Button
          path="mailto:support@sieloapp.com"
          color="white"
          ml="10px"
          mr="10px"
          variant="texted"
        >
          Contact Support
        </Button>
        |
        <Button
          path="/signin"
          color="white"
          ml="10px"
          mr="10px"
          variant="texted"
        >
          Sign In
        </Button>
        |
        <Button path="/signup" color="white" ml="10px" variant="texted">
          Sign Up
        </Button>
      </StyledBtnWrap>
    </StyledWrap>
  );
};

export default VerifyContainer;
