const xorCipher = (text: string) => {
  const cryptoKey = process.env.REACT_APP_CRYPTO_KEY as string;
  let output = '';
  for (let i = 0; i < text.length; i++) {
    const charCode =
      text.charCodeAt(i) ^ cryptoKey.charCodeAt(i % cryptoKey.length);
    output += String.fromCharCode(charCode);
  }
  return output;
};

export default xorCipher;
